
* {
    text-align: center;
    font-size: 100%;
    font-family: 'NanumSquareAcl';
    color: #6e6a67;
}

.fontBold {
    font-family: 'NanumSquareAceb';
}

.fontRegular {
    font-family: 'NanumSquareAcer';
}

.first {
    flex: 1;
    margin-right: 70%;
}

.second {
    flex: 1;
}

.mapDiv {
    display: flex;
    justify-content: center;
    align-items: center;
}

.customoverlay {
    position: relative;
    bottom: 85px;
    border-radius: 6px;
    border: 1px solid #ccc;
    border-bottom: 2px solid #ddd;
    float: left;
}

.customoverlay:nth-of-type(n) {
    border: 0;
    box-shadow: 0px 1px 2px #888;
}

.customoverlay a {
    display: block;
    text-decoration: none;
    color: #000;
    text-align: center;
    border-radius: 6px;
    font-size: 14px;
    font-weight: bold;
    overflow: hidden;
    background: #ffaea1;
    background: #ffaea1 url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/arrow_white.png) no-repeat right 14px center;
}

.customoverlay .title {
    display: block;
    text-align: center;
    background: #fff;
    margin-right: 35px;
    padding: 10px 15px;
    font-size: 14px;
    font-weight: bold;
}

.customoverlay:after {
    content: '';
    position: absolute;
    margin-left: -12px;
    left: 50%;
    bottom: -12px;
    width: 22px;
    height: 12px;
    background: url('https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/vertex_white.png')
}

.titleDiv {
    font-family: 'NanumSquareAceB';
    font-size: 100%;
    padding-top: 20%;
    padding-bottom: 5%;
}